header {
  background-color: #fff;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 3;
}
header .row-header {
  padding: 10px 40px;
  border-bottom: 2px solid #dedede;
  align-items: center;
}
header .row-header img {
  width: 150px;
}
header .row-header .menu-header {
  display: flex;
  justify-content: flex-end;
}
header .row-header .menu-header #nav-desk {
  display: flex;
  align-items: center;
}
header .row-header .menu-header #nav-desk ul {
  display: flex;
  gap: 50px;
  padding: 0;
  margin-bottom: 0;
}
header .row-header .menu-header #nav-desk ul li {
  list-style: none;
}
header .row-header .menu-header #nav-desk ul li a {
  text-decoration: none;
  color: #000;
  font-weight: 300;
  transition: all ease 0.3s;
}
header .row-header .menu-header #nav-desk ul li a:hover {
  color: #ec0001;
}
header .row-header .menu-header #nav-desk ul li.current_page_item a {
  color: #ec0001;
}
header .row-header .menu-header #menu-mobile {
  display: none;
}
header .row-header .menu-header #menu-mobile #nav-mobile {
  width: 0;
  background: #fff;
  position: fixed;
  left: -1000px;
  top: 0;
  height: 100%;
  padding: 30px 0;
  opacity: 0;
  z-index: -1;
  transition: all ease 0.3s;
}
header .row-header .menu-header #menu-mobile #nav-mobile li {
  list-style: none;
  margin: 15px 0;
}
header .row-header .menu-header #menu-mobile #nav-mobile li a {
  font-size: 20px;
  color: #ec0001;
  text-decoration: none;
  font-weight: 600;
}
header .row-header .menu-header #menu-mobile #nav-mobile.active {
  left: 0;
  z-index: 100;
  opacity: 1;
  width: 100%;
}
header .row-header .div-search i {
  color: #ec0001;
  border-left: 1px solid #ec0001;
  padding-left: 5px;
  cursor: pointer;
  transition: all ease 0.3s;
}
header .row-header .div-search i:hover {
  color: #404040;
  border-color: #404040;
}
header .row-header .div-search form input {
  width: 0;
  padding: 0;
  border: none;
  outline: none;
  border-radius: 20px;
  transition: width 0.3s ease, padding 0.4s ease;
  opacity: 0;
  transform: translateX(20px);
}
header .row-header .div-search form.active input {
  width: 100%;
  padding: 5px 10px;
  opacity: 1;
  transform: translateX(0);
  border: 1px solid #ccc;
}